<template>
  <div class="my-navigation">
    <div class="nav_bar" @click="$store.commit('toggleMenu')">
      <i class="fa fa-bars"></i>
    </div>
    <nav class="nav_section">
      <div class="personal_logo"></div>
      <div class="item" v-for="(route, i) in routes" :key="i" @click="go(route)" :class="{ 'active': activeRoute === route }">{{ route }}</div>
      <br>
      <div class="icon_item" @click="toggleModal()">
        <div class="tooltip">Contact me</div>
        <i class="fa fa-envelope"></i>
      </div>
      <div class="icon_item" @click="openLink('https://www.linkedin.com/in/dallin-stagg')">
        <div class="tooltip">LinkedIn</div>
        <i class="fab fa-linkedin-in"></i>
      </div>
      <div class="icon_item" @click="openLink('https://github.com/Dallin-Stagg')">  
        <div class="tooltip">Github</div>
        <i class="fab fa-github"></i>
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
  data() {
    return {
      routes: ['Home', 'Experience', 'Portfolio']
    }
  },
  computed: {
    activeRoute() {
      return this.$route.name
    },
    menuOpen() {
      return this.$store.state.menuOpen
    }
  },
  methods: {
    go(routeName) {
      this.toggleMenu()
      this.$router.push({ name: routeName })
    },
    openLink(link) {
      window.open(link)
    },
    toggleModal() {
      this.toggleMenu()
      this.$store.commit('toggleModal')
    },
    toggleMenu() {
      if (this.menuOpen) {
        this.$store.commit('toggleMenu')
      }
    }
  }
}
</script>
<style scoped lang="scss">
@import '../assets/styles/global-styles.scss';
.nav_bar {
  background-color: rgba($white, .95);
  border-radius: 1.5rem;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
  cursor: pointer;
  height: 3rem;
  width: 3rem;
  text-align: center;
  position: fixed;
  top: 1rem;
  left: 1rem;
  user-select: none;
  z-index: 5;
  @include mobile {
    text-align: left;
    padding: 0 1.5rem;
    width: calc(100% - 2rem);
  }
  i {
    color: $gray-dk;
    line-height: 3rem;
  }
}
.nav_section {
  position: absolute;
  left: 0;
  top: 5rem;
  .personal_logo {
    background-image: url('../assets/images/logo-white.png');
    background-size: 190%;
    background-repeat: no-repeat;
    background-position: center;
    filter: drop-shadow(0px 2px 5px rgba(0,0,0,0.5));
    height: 8rem;
    width: 12rem;
  }
  .item {
    border: 1px solid $white;
    border-radius: 3.1rem;
    color: $white;
    cursor: pointer;
    font-size: .95rem;
    line-height: 2.3rem;
    margin: .6rem 1rem;
    padding: 0 1rem;
    text-shadow: 0px 2px 10px rgba(34, 77, 97, 0.7);
    width: 7.5rem;
    @include mobile {
      font-size: 1.1rem;
      line-height: 3rem;
      padding: 0 1.5rem;
      width: 6.5rem;
    }
    &:hover {
      background-color: rgba($white, .05);
    }
    &.active {
      background-color: rgba($white, .3);
    }
  }
  .icon_item {
    border: 1px solid $white;
    border-radius: 3.1rem;
    color: $white;
    cursor: pointer;
    line-height: 2.5rem;
    margin: .75rem 1rem;
    position: relative;
    text-align: center;
    width: 2.5rem;
    text-shadow: 0px 2px 10px rgba(34, 77, 97, 0.7);
    @include mobile {
      font-size: 1.1rem;
      line-height: 3rem;
      width: 3rem;
    }
    &:hover {
      background-color: rgba($white, .1);
      .tooltip {
        display: block;
        @include mobile {
          display: none;
        }
      }
    }
    .tooltip {
      background-color: rgba($white, .5);
      border-radius: 1rem;
      color: $text-primary;
      display: none;
      font-size: .9rem;
      font-weight: 500;
      line-height: 2rem;
      position: absolute;
      top: .3rem;
      left: 3rem;
      text-shadow: none;
      width: 8rem;
    }
  }
}
</style>
